<template>
	<div class="albumdetail">
		
		<!-- 专辑头部 -->
		<div class="albumhead">
			   <!-- 歌单封面 -->
			   <el-image :src="albumDetail.picUrl" style="border-radius: 10px; height: 100%; width: 200px; position: absolute"></el-image>
			   <!-- 歌单名 -->
			   <div class="listName">
				 <div style="color: #d03535;
					 border: 1px solid #d03535;
					 display: inline-block;
					 font-size: 20px;
					 margin-right: 10px;
					 border-radius: 5px;
					 padding: 0 7px;
					 cursor: default; "> 专辑</div>
				 <span v-if="!!albumDetail">{{ albumDetail.name }}</span>
				 <div v-else>{{""}}</div>
			   </div>
			   
			  <div class="listIntro">
				 <div>
					 歌手： 
				     <span v-if="!!albumDetail.artist">{{ albumDetail.artist.name }}</span>
				 <div v-else>{{""}}</div>
				 </div>
			    <div style="display: inline-block">
				   发行时间：{{albumDetail.publishTime|dateFormat("")}}
				 </div>
				 
				 
				 <div class="orbtn">
					 
				   <i class="el-icon-share" style="font-size: 20px"></i> 分享量
				   <span v-if="!!albumDetail.info">
					   ({{
					     albumDetail.info.shareCount >= 10000? (albumDetail.info.shareCount / 10000).toFixed(0) + "万"
					       : albumDetail.info.shareCount
					   }})
				   </span>
				   <div v-else>{{""}}</div>
				 </div>
				 
			  </div>
		</div>
		
		<!-- 专辑介绍 -->
		<div class="describe">
			<el-collapse>
			  <el-collapse-item title="专辑介绍:">
				<div v-if="!!albumDetail.description">
				<p v-for="(item, index) in textFormat(albumDetail.description)" :key="index" 
				style="opacity: 0.8; text-indent: 30px; letter-spacing: 0px; line-height: 25px">
				  {{ item }}</p>
				</div>
				<div v-else>{{""}}</div>
				</el-collapse-item>
			</el-collapse>
		</div>
	<div class="medium">
		<!-- 导航栏 -->
	    <detailTab ref="detailTab" @changeActive="changeActive" :commentCount="total"  />
	
		<!-- 专辑歌曲列表 -->
		 <!--  <span style="font-weight: 600; font-size: 18px;">包含歌曲列表:</span>
		   <hr /> -->
		   <musicList  v-show="activeIndex === '1'"  :songs="songs" />
	       <!-- 评论-->
	   		<comment :commentList="commentList" :total="total"  v-show="activeIndex === '2'"  @getCommentPage="getCommentPage"/>
	   </div>
	    
	   <br /><br /><br /><br />
	   
	   <!-- 底部播放器 -->
	    <el-footer class="el-footer">
			<div class="img"></div>
			<a href="#">
			<div class="mask">
				<!-- h5标签 -->
				<audio :src="$store.state.musicUrl" style="width: 100%;" controls="controls" autoplay="autoplay" @play="play" @pause="pause">
					当前浏览器不支持audio播放
				</audio>
			</div>
			</a>
	   </el-footer>
	    
	</div>
</template>

<script>
	
	import musicList from "../../components/Allalbum/musicList.vue";
	import detailTab from "../../components/Allalbum/detailTab.vue";
	import comment from "../../components/Allalbum/comment.vue";
	export default {
		 name:"albumdetail",
		 components: {
		   musicList,
		   detailTab,
		   comment
		 },
		 
		 data() {
		   return {
		     // 传入的id
		     currentId: this.$route.params.id,
		     // 专辑数据
		     albumDetail: [],
			
			 // 导航页面
			 activeIndex: "1",
			 //歌曲的url
			 musicUrl:"",
			 // 歌曲数据
			 songs: [],
			 //判断是否播放
			 isPlaying:false,
			 // 评论数据
			 commentList: [],
			 total:0,
		   };
		 },
		 methods: {
			 // 获取专辑详情数据
		 async getAlbumDetail() {
				 var that=this;
				 // debugger;
				 /* 歌曲以及专辑详情*/
				 this.$axios.get("https://autumnfish.cn/album?id="+this.currentId)
					.then((response) => {
						 that.albumDetail = response.data.album;
						 that.songs = response.data.songs;
				},function(err){})
				this.getCommentPage(0);
			
				
				/* 评论 */
				/* this.$axios.get("https://autumnfish.cn/comment/album?limit=10&id="+this.currentId) */
				/* this.$axios.get("https://autumnfish.cn/comment/album?type=3&limit=10&id="+this.currentId)
					.then((response) => { */
						 /* console.log(response.data.comments); */
						/* that.commentList= response.data.hotComments; */
						/* that.commentList= response.data.comments;
						 that.total = response.data.total;
				},function(err){}) */
			},
			// 获取评论数据
			getCommentPage(page) {
				 var that=this;
				 this.$axios.get("https://autumnfish.cn/comment/album?limit=10"+"&id="+this.currentId+"&offset="+page*10)
			    .then((response) => {
					/*  console.log(response.data.comments); */
			        that.commentList = response.data.comments;
					that.total = response.data.total;
			    });
			},
					
			//专辑介绍文字规范
			textFormat(text) {
			  let newText = text.split("\n");
			  return newText;
			},
			//获取组件切换的值
			changeActive(index){
				 this.activeIndex = index;
			},
			//是否播放
			play(){
				this.isPlaying=true;
			},
			pause(){
				this.isPlaying=false;
			},
			
								
		},
		created() {
		  this.getAlbumDetail();
		},
			 
	}
	
</script>

<style scoped>
/* 歌单细节 */
.albumdetail .albumhead {
  position: relative;
  margin-left: 90px;
  margin-top: 20px;
  width: 90%;
  height: 200px;
}

/* 歌单名 */
.listName {
  position: absolute;
  font-size: 18px;
  font-weight: 600;
  left: 300px;
  top: 7%;
}

/* 介绍 */
.listIntro {
  position: absolute;
  height: 30%;
  left: 300px;
  top: 40%;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 10px;
}
/* 中间列表 */
.medium{
	margin-left:100px;
}
/* 专辑介绍 */
.describe {
  width: 80%;
  margin:25px auto;
}

.el-footer{
	/* position: fixed; */
	position: fixed;
	bottom: 0;
	width: 80%;
	left: 8%;
}
.mask{
	position: absolute;
	width: 80%;
	left: 8%;
	opacity:0.2;
	-webkit-transition:1s;
}
.mask audio{
	text-align: center;
	
}
.el-footer a:hover .mask{
	opacity: 1;
}
</style>