<template>
	<div class="comment">	
				<!-- 判断是否有评论 -->
				<div v-if="total==0"><h2>暂时没有热门评论哦</h2></div>
				<div v-else>
					<div  v-for="(item, index) in commentList" :key="index"
					  style=" position: relative;   width:87%;   border-bottom: 1px solid rgb(240, 240, 242);
					    padding: 7px 0;
					    font-size: 15px; ">
					  <div>
					    <!--用户头像-->
					     <img :src="item.user.avatarUrl" style="display: inline; width: 45px; height: 45px;  border-radius: 25px;  cursor: pointer;"/>
					
					    <!--评论信息-->
					    <div style="margin-left: 60px; margin-top: -50px">
					      <p>
					        <span style="color: #4d99de; cursor: pointer">{{ item.user.nickname }}</span>: {{ item.content }}
					      </p>
					    </div>
					  </div>
					</div>
					
					<el-pagination
					  background
					  layout="prev, pager, next"
					  :total="Math.ceil(total / 20) * 10"
					  :pager-count="9"
					  @current-change="getCommentPage"
					  :current-page.sync="currentPage"
					>
					</el-pagination>
					
					<br /><br /><br />
				</div>
			
			
	</div>
</template>

<script>
	export default {
		name: "comment",
		props: {
		  // 评论数据
		  commentList: Array,
		  // 评论总数
		  total:"",
		  
		},
		data() {
		  return {
		    commentInfo: "",
			//最新页
			currentPage: 1,
		  };
		},
		methods: {
			// 获取评论页
			getCommentPage(page) {
			  this.$emit("getCommentPage", page-1);
			},
		},
	}
</script>

<style scoped>
  .el-row {
     margin-bottom: 20px;
     &:last-child {
       margin-bottom: 0;
     }
   }
   .el-col {
     border-radius: 4px;
   }
   .bg-purple {
     background: #d3dce6;
   }
   .bg-purple-light {
     background: #e5e9f2;
   }
   .grid-content {
     border-radius: 4px;
     min-height: 36px;
   }
   .row-bg {
     padding: 10px 0;
     background-color: #f9fafc;
   }
</style>