<template>
	<div  class="musicList">
		
			   <el-table :data="songs"  stripe  style="width: 87%" :default-sort = "{prop: 'date', order: 'descending'}">
				
				 <el-table-column
					prop="id"
					label="点击播放"
					width="150">
					<template slot-scope="scope">
					  <i class="el-icon-caret-right" @click='playMusic(scope.row.id)'></i>
						<img v-show="scope.row.mv!= 0" class="image" src="../../assets/images/mv.jpg"  @click='playMv(scope.row.mv)'/>
					</template>
				  </el-table-column>			
					  
				   <el-table-column
					  prop="name"
			          label="歌曲名称"
					   sortable
			          width="350">
			        </el-table-column>
			        <el-table-column
			          prop="ar[0].name"
			          label="歌手"
			          width="200">
			        </el-table-column>
					<!--  <el-table-column
						prop="al.name"
						label="专辑"
						width="200">
					  </el-table-column> -->			
					<el-table-column
					  prop="dt"
					  label="时长"
					  sortable
					  width="200">
					 <template slot-scope="scope">
					    <span>{{scope.row.dt/1000|timeFormat("")}}</span>
					  </template>
					</el-table-column>
			      </el-table>
				  
				  <!-- 视频播放 -->
				 <div class="mask" v-show="isPlayMv">
					<i class="el-icon-close" @click='hide'></i> 
					<video class="center-block" :src="$store.state.mvUrl2" controls="controls" autoplay="autoplay"></video>
				  </div>
	</div>
</template>

<script>
	export default {
		name: "musicList",
		
		data(){
			return {
			  //歌曲的url
			  musicUrl:"",
			  //mv地址
			  mvUrl:"",
			  //是否播放mv
			  isPlayMv:false,
			};
		},
		props: {
		  // 歌曲搜索信息
		  songs: Array,
		},
		
		methods: {
			 formatter(row, column) {
			        return row.address;
			      },
			  //根据id获取音乐url
			  async playMusic(id) {
				  var that=this;
				   // debugger;
				  this.$axios.get("https://autumnfish.cn/song/url?id="+id)
				  .then(function(response){
					that.musicUrl=response.data.data[0].url;
					/* console.log(response.data.data[0].url) */
					that.$store.commit("musicUrl",that.musicUrl);
				  },function(err){})
				},
				
				
				//根据mvid获取mvurl
				async playMv(mvid) {
					  var that=this;
					  console.log(mvid)
					   // debugger;
					  this.$axios.get("https://autumnfish.cn/mv/url?id="+mvid)
					  .then(function(response){
							that.isPlayMv=true;
							console.log(response.data.data.url)
							that.mvUrl=response.data.data.url;
							that.$store.commit("changeMv2",that.mvUrl);
					  },function(err){})
					},
					hide(){
						this.isPlayMv=false;
					}
								    				
		},
	}	
</script>

<style scoped>
	
	.el-icon-caret-right{
		cursor: pointer;
		font-size: 15px;
	}
	.el-icon-caret-right:hover{
		cursor: pointer;
		font-size: 25px;
	}
	.image{
		cursor: pointer;
		width: 10px;
		height: 10px;
	}
	.image:hover{
		cursor: pointer;
		width: 15px;
		height: 15px;
	}
	.mask{
			width: 82%;
			height: 550px;
			position:absolute;
			z-index: 10;
			background-color: rgba(0, 0, 0, 0.6);
			top:400px;
			left:9%;
		}
		.mask video{
			margin-top: 25px;
			width:100%;
			height: 489px;
		}
		.el-icon-circle-close-outline{
			cursor: pointer;
			font-size: 30px;
		}
	 .el-icon-close{
			margin-top: 55px;
			float: left;
			margin-top: 5px;
			margin-left: 93%;
			font-size: 30px;
			
		}

</style>